.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 340px;
}

.heading {
  margin-top: 40px;
  margin-bottom: 20px;
  position: relative;
  align-self: flex-start;
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
